<template>
  <v-layout row wrap>
    <v-flex v-if="loggedIn">
        <v-slide-y-transition>
          <v-card-text v-show="show">
            Example text
          </v-card-text>
        </v-slide-y-transition>
    </v-flex>
    <v-flex v-else xs4 offset-xs4 md4 offset-md4>
      <v-btn large outline color="indigo" @click="backToLogin()">
        <v-icon left dark class="ml-1">keyboard_return</v-icon>
        Not login yet, back to login
      </v-btn>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'home',
  data: function () {
    return {
      show: true
    }
  },
  methods: {
    ...mapActions('user', {
      login: 'login',
      logout: 'logout'
    }),
    backToLogin: function () {
      this.$router.push('/')
    }
  },
  computed: {
    loggedIn: function () {
      if (sessionStorage.getItem('loggedIn')) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>


