<template>
  <v-app dark>    
    <v-content class="zindex">
      <v-container>
        <vue-particles 
      color="#dedede"
      :particleOpacity="0.7"
        :particlesNumber="80"
        shapeType="circle"
        :particleSize="4"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
        class="particles-js"
      ></vue-particles>
        <v-slide-y-transition mode="out-in">
          <router-view></router-view>
        </v-slide-y-transition>
      </v-container>
    </v-content>
    <my-footer class="zindex"></my-footer>
  </v-app>
</template>

<script>
import MyFooter from '@/components/MyFooter'
export default {
  name: 'app',
  components: { MyFooter }
}
</script>

<style>
body {
  margin: 0;
}

main {
  text-align: center;
  margin-top: 40px;
}

.zindex {
  z-index: 999;
}

.particles-js {
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
</style>



