<template>
  <v-layout row wrap>
    <v-flex xs4 offset-xs4 md4 offset-md4>
      <v-form v-model="valid" ref="form" lazy-validation v-if="!loggedIn">
          <v-text-field
              label="Key"
              v-model="key"
              :rules="keyRules"
              :counter="5"
              required
              @keyup.enter.native="login(key)"
          ></v-text-field>
          <v-btn outline  color="indigo" @click="login(key)" :disabled="!valid">
            Go
            <v-icon right dark class="ml-1">keyboard_tab</v-icon>
          </v-btn>
        </v-form>
        <v-alert color="green lighten-2" icon="info" :value="true" v-if="error_message !== ''">
            {{error_message}}
          </v-alert>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'login',
  data: function () {
    return {
      valid: false,
      key: '',
      keyRules: [
        v => !!v || 'Key is required',
        v => (v && v.length === 5) || 'Key must be equal 5 characters'
      ]
    }
  },
  methods: {
    ...mapActions('user', {
      login: 'login',
      logout: 'logout'
    })
  },
  computed: {
    ...mapGetters('user', {
      loggedIn: 'loggedIn',
      error_message: 'error_message'
    })
  }
}
</script>
<style>

</style>



